.container {
  margin-bottom: 45px;
}
.itemContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.item {
  font-size: 16px;
}
.title {
  font-size: 22px;
  margin-bottom: 18px;
}
.countCircle {
  background-color: #d9e0c9a6;
  color: #4a8015;
  padding: 2px 10px;
  border-radius: 12px;
  font-weight: 500;
}
.itemContainer:not(:last-child) {
  margin-bottom: 10px;
}
